const app = getApp();
import helper from '../common/helper.js';
import uniPopup from '@/components/uni-popup.vue';
export default {
	data() {
		return {
			tableData: [
				{ name: '大锤', age: '17777777777', address: '2019-10-25' }, 
				{ name: '张三', age: '21', address: '成都' }, 
				{ name: '李四', age: '16', address: '南京' },
			],
			columns: [
				{ title: '用户名', key: 'name' }, 
				{ title: '手机号', key: 'age' }, 
				{ title: '时间', key: 'address' },
			],
			bottomData: [
				{
					text: '微信好友',
					type: 'WXSceneSession',
					icon: 'iconweixin'
				},
				{
					text: '微信朋友圈',
					type: 'WXSenceTimeline',
					icon: 'iconpengyouquan'
				}
			],
			collectionList: {},
			imgUrl: '',
			isShow: true,
			cancelShow: true,
			inputValue: ''
		};
	},
	components: {
		// wTable,
		uniPopup
	},
	onShow() {
		this.content();
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
	},
	// 微信分享
	onShareAppMessage() {
		let url = this.getPageUrl()
		return {
			title: this.articleDetail.title,
			path: url
		}
	},
	// shareFriend() {
	// 	//分享到微信朋友
	// 	this.goShare('WXSceneSession');
	// },
	// shareFriendcricle() {
	// 	//分享到微信朋友圈
	// 	this.goShare('WXSenceTimeline');
	// },
	methods: {
		withdraw(){
			this.cancelShow = false
		},
		cancelModel(e){
			console.log(e,'888')
			if(e == '2'){
				this.cancelShow = true
			}else if(e == "1"){
				this.getWithdraw()
			}
		},
		getWithdraw(){
			if(this.inputValue == ''){
				uni.showToast({
					title: '请输入提现金额',
					icon: 'none'
				})
			}
			if(this.inputValue > this.collectionList.user_blance){
				uni.showToast({
					title: '输入提现金额不能大于余额',
					icon: 'none'
				})
			}
			uni.request({
				url: `${helper.requestUrl}/user/cash-withdrawals-apply`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					money: this.inputValue
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.cancelShow = true
						uni.showToast({
							title: '提现成功',
							icon: 'success'
						})
						this.content();
						// setTimeout(() => {
							
						// }, 1000)
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
				}
			});
		},
		getInput(e){
			console.log(e,'999')
			this.inputValue = e.detail.value
		},
		goMore(){
			this.isShow = false
		},
		// 获取当前页路径及参数,用于分享
		getPageUrl() {
			// pages/articleDetail?id=5&name=222&aaa=2344asfdasdf
			// let options = {id: '5', name: '222', aaa: '2344asfdasdf'}
			let pageNode = getCurrentPages()
			pageNode = pageNode[pageNode.length - 1]
			let url = pageNode.route
			let options = pageNode.options
			let optionsString = '?'
			for( let key in options ){
					optionsString += key
					optionsString += '='
					optionsString += options[key]
					optionsString += '&'
			}
			optionsString = optionsString.substring(0, optionsString.length - 1)
			url += optionsString
			return url
		},
		quickInlet(e) {
			if (e == 1) {
				this.$refs.showshare.open();
			} else if (e == 2) {
				this.$refs.center.open();
			}
			// console.log(e);
		},
		cancel(type) {
			this.$refs['show' + type].close();
		},
		content() {
			uni.request({
				url: `${helper.requestUrl}/promote-rebates`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					// console.log(res);
					if (res.data.status_code == 200) {
						this.collectionList = res.data;
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
				}
			});
		},
		// goShare(WXSenceType) {
		// 	// 获取页面路径
		// 	console.log(this.collectionList,'**')
		// 	uni.showToast({
		// 		title: this.collectionList.share_link,
		// 				icon: 'none',
		// 				duration: 300000000
		// 	});
		// 	return false
		// 	// let url = this.getPageUrl()
		// 	console.log(url)
		// 	uni.share({
		// 		provider: 'weixin',
		// 		scene: WXSenceType,
		// 		type: 0,
		// 		href: url,
		// 		title: '新微金论坛',
		// 		summary: this.collectionList.share_link,
		// 		imageUrl: '',
		// 		success: function(res) {
		// 			console.log('success:' + JSON.stringify(res));
		// 		},
		// 		fail: function(err) {
		// 			console.log('fail:' + JSON.stringify(err));
		// 		},
		// 	});
		// },
		goShare(e) {
			console.log(e)
			let sceneType = ''
			if(e == 'WXSceneSession'){
				sceneType = 'WXSceneSession'
			} else if(e == 'WXSenceTimeline'){
				sceneType = 'WXSenceTimeline'
			}
			uni.share({
			    provider: "weixin",
			    scene: sceneType,
			    type: 0,
			    href: this.collectionList.share_link,
			    title: "新微金论坛",
			    summary: "我正在使用新微金论坛，赶紧跟我一起来体验！",
			    imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
			    success: function (res) {
			        console.log("success:" + JSON.stringify(res));
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			});
		},
		// 团队列表
		goTeam(e){
			console.log(e)
			let name = ''
			if(e == 1){
				name = '团队长列表'
			}else{
				name = '团队列表'
			}
			uni.navigateTo({
				url: `/pages/meTeamList?name=${name}`
			})
		}
	}
};
